export default{
	data(){
		return{
			imgSrc:'',
			photo:true
		}
	},
	methods:{
		upload(){
			let that = this
			uni.chooseImage({
				count:1,
				success(res) {
					that.imgSrc = res.tempFilePaths[0]
					that.photo = false
				}
			})
		}
	}
}
